import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Grid,
  useTheme,
  Chip,
  IconButton,
  Tooltip,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  LinearProgress,
} from '@mui/material';
import {
  Speed as SpeedIcon,
  Security as SecurityIcon,
  DirectionsCar as CarIcon,
  Warning as WarningIcon,
  Refresh as RefreshIcon,
  Fullscreen as FullscreenIcon,
} from '@mui/icons-material';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip as RechartsTooltip,
  ResponsiveContainer,
  BarChart,
  Bar,
  PieChart,
  Pie,
  Cell,
  RadarChart,
  PolarGrid,
  PolarAngleAxis,
  PolarRadiusAxis,
  Radar,
} from 'recharts';
import { vehicleService, VehicleAnalytics, Vehicle } from '../../services/vehicleService';

const Analytics: React.FC = () => {
  const [analytics, setAnalytics] = useState<VehicleAnalytics | null>(null);
  const [vehicles, setVehicles] = useState<Vehicle[]>([]);
  const [timeRange, setTimeRange] = useState<'1h' | '24h' | '7d' | '30d'>('24h');
  const theme = useTheme();

  useEffect(() => {
    const unsubscribeAnalytics = vehicleService.subscribeToAnalytics((data) => {
      setAnalytics(data);
    });

    const unsubscribeVehicles = vehicleService.subscribeToVehicles((data) => {
      setVehicles(data);
    });

    return () => {
      unsubscribeAnalytics();
      unsubscribeVehicles();
    };
  }, []);

  // Generate time series data for charts
  const generateTimeSeriesData = () => {
    const data = [];
    const now = new Date();
    const hours = timeRange === '1h' ? 1 : timeRange === '24h' ? 24 : timeRange === '7d' ? 168 : 720;
    
    for (let i = hours; i >= 0; i--) {
      const time = new Date(now.getTime() - i * (timeRange === '1h' ? 60 * 60 * 1000 : timeRange === '24h' ? 60 * 60 * 1000 : 24 * 60 * 60 * 1000));
      data.push({
        time: time.toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' }),
        vehicles: Math.floor(Math.random() * 20) + 40,
        speed: Math.random() * 30 + 35,
        trust: Math.random() * 20 + 80,
        incidents: Math.floor(Math.random() * 5),
        network: Math.random() * 20 + 80,
      });
    }
    return data;
  };

  // Generate vehicle performance data
  const generateVehiclePerformanceData = () => {
    return vehicles.slice(0, 10).map(vehicle => ({
      name: vehicle.name,
      speed: vehicle.velocity.speed,
      trust: vehicle.security.trustScore,
      fuel: vehicle.sensors.fuel,
      battery: vehicle.sensors.battery,
      network: 100 - vehicle.network.latency / 2,
    }));
  };

  // Generate security metrics
  const generateSecurityData = () => {
    const validCertificates = vehicles.filter(v => v.security.certificateValid).length;
    const highTrust = vehicles.filter(v => v.security.trustScore > 90).length;
    const mediumTrust = vehicles.filter(v => v.security.trustScore > 70 && v.security.trustScore <= 90).length;
    const lowTrust = vehicles.filter(v => v.security.trustScore <= 70).length;

    return [
      { name: 'Valid Certificates', value: validCertificates, color: theme.palette.success.main },
      { name: 'High Trust', value: highTrust, color: theme.palette.primary.main },
      { name: 'Medium Trust', value: mediumTrust, color: theme.palette.warning.main },
      { name: 'Low Trust', value: lowTrust, color: theme.palette.error.main },
    ];
  };

  // Generate network performance data
  const generateNetworkData = () => {
    const connectionTypes = ['4G', '5G', 'WiFi', 'Satellite'];
    const data = connectionTypes.map(type => ({
      type,
      vehicles: vehicles.filter(v => v.network.connectionType === type).length,
      avgLatency: vehicles
        .filter(v => v.network.connectionType === type)
        .reduce((sum, v) => sum + v.network.latency, 0) / 
        Math.max(vehicles.filter(v => v.network.connectionType === type).length, 1),
    }));

    return data;
  };

  if (!analytics) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '50vh' }}>
        <Typography>Loading analytics...</Typography>
      </Box>
    );
  }

  const timeSeriesData = generateTimeSeriesData();
  const vehiclePerformanceData = generateVehiclePerformanceData();
  const securityData = generateSecurityData();
  const networkData = generateNetworkData();

  return (
    <Box sx={{ p: { xs: 2, md: 3 }, pt: { xs: 8, md: 10 } }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">
          Vehicle Analytics Dashboard
        </Typography>
        <Box sx={{ display: 'flex', gap: 1 }}>
          <Tooltip title="Refresh">
            <IconButton color="primary" onClick={() => window.location.reload()}>
              <RefreshIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="Fullscreen">
            <IconButton color="primary">
              <FullscreenIcon />
            </IconButton>
          </Tooltip>
        </Box>
      </Box>

      {/* Time Range Selector */}
      <Box sx={{ mb: 3 }}>
        <Box sx={{ display: 'flex', gap: 1 }}>
          {(['1h', '24h', '7d', '30d'] as const).map((range) => (
            <Chip
              key={range}
              label={range}
              color={timeRange === range ? 'primary' : 'default'}
              onClick={() => setTimeRange(range)}
              variant={timeRange === range ? 'filled' : 'outlined'}
            />
          ))}
        </Box>
      </Box>

      {/* Key Metrics */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <Box>
                  <Typography color="text.secondary" gutterBottom>
                    Total Vehicles
                  </Typography>
                  <Typography variant="h4">
                    {analytics.totalVehicles}
                  </Typography>
                  <Chip
                    label={`${analytics.activeVehicles} active`}
                    color="success"
                    size="small"
                    sx={{ mt: 1 }}
                  />
                </Box>
                <CarIcon sx={{ fontSize: 40, color: 'primary.main' }} />
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <Box>
                  <Typography color="text.secondary" gutterBottom>
                    Average Speed
                  </Typography>
                  <Typography variant="h4">
                    {analytics.averageSpeed.toFixed(1)}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    km/h
                  </Typography>
                </Box>
                <SpeedIcon sx={{ fontSize: 40, color: 'success.main' }} />
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <Box>
                  <Typography color="text.secondary" gutterBottom>
                    System Health
                  </Typography>
                  <Typography variant="h4">
                    {analytics.systemHealth.toFixed(1)}%
                  </Typography>
                  <LinearProgress
                    variant="determinate"
                    value={analytics.systemHealth}
                    sx={{ mt: 1, height: 8, borderRadius: 4 }}
                  />
                </Box>
                <SecurityIcon sx={{ fontSize: 40, color: 'success.main' }} />
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <Box>
                  <Typography color="text.secondary" gutterBottom>
                    Security Incidents
                  </Typography>
                  <Typography variant="h4">
                    {analytics.securityIncidents}
                  </Typography>
                  <Chip
                    label="Monitor"
                    color={analytics.securityIncidents > 0 ? 'warning' : 'success'}
                    size="small"
                    sx={{ mt: 1 }}
                  />
                </Box>
                <WarningIcon sx={{ fontSize: 40, color: analytics.securityIncidents > 0 ? 'warning.main' : 'success.main' }} />
              </Box>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Charts */}
      <Grid container spacing={3}>
        {/* Time Series Chart */}
        <Grid item xs={12} lg={8}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                System Performance Over Time
              </Typography>
              <ResponsiveContainer width="100%" height={300}>
                <LineChart data={timeSeriesData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="time" />
                  <YAxis />
                  <RechartsTooltip />
                  <Line type="monotone" dataKey="vehicles" stroke={theme.palette.primary.main} strokeWidth={2} />
                  <Line type="monotone" dataKey="speed" stroke={theme.palette.success.main} strokeWidth={2} />
                  <Line type="monotone" dataKey="trust" stroke={theme.palette.warning.main} strokeWidth={2} />
                </LineChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </Grid>

        {/* Security Distribution */}
        <Grid item xs={12} lg={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Security Trust Distribution
              </Typography>
              <ResponsiveContainer width="100%" height={300}>
                <PieChart>
                  <Pie
                    data={securityData}
                    cx="50%"
                    cy="50%"
                    innerRadius={60}
                    outerRadius={80}
                    paddingAngle={5}
                    dataKey="value"
                  >
                    {securityData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <RechartsTooltip />
                </PieChart>
              </ResponsiveContainer>
              <Box sx={{ display: 'flex', justifyContent: 'space-around', mt: 2 }}>
                {securityData.map((item) => (
                  <Box key={item.name} sx={{ textAlign: 'center' }}>
                    <Box sx={{ width: 12, height: 12, bgcolor: item.color, borderRadius: '50%', mx: 'auto', mb: 1 }} />
                    <Typography variant="caption" color="text.secondary">
                      {item.name}
                    </Typography>
                  </Box>
                ))}
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/* Vehicle Performance Table */}
        <Grid item xs={12} lg={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Top Vehicle Performance
              </Typography>
              <TableContainer component={Paper} variant="outlined">
                <Table size="small">
                  <TableHead>
                    <TableRow>
                      <TableCell>Vehicle</TableCell>
                      <TableCell align="right">Speed (km/h)</TableCell>
                      <TableCell align="right">Trust (%)</TableCell>
                      <TableCell align="right">Network (%)</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {vehiclePerformanceData.map((vehicle) => (
                      <TableRow key={vehicle.name}>
                        <TableCell component="th" scope="row">
                          {vehicle.name}
                        </TableCell>
                        <TableCell align="right">{vehicle.speed.toFixed(1)}</TableCell>
                        <TableCell align="right">{vehicle.trust.toFixed(1)}</TableCell>
                        <TableCell align="right">{vehicle.network.toFixed(1)}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </CardContent>
          </Card>
        </Grid>

        {/* Network Performance */}
        <Grid item xs={12} lg={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Network Performance by Type
              </Typography>
              <ResponsiveContainer width="100%" height={300}>
                <BarChart data={networkData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="type" />
                  <YAxis />
                  <RechartsTooltip />
                  <Bar dataKey="vehicles" fill={theme.palette.primary.main} />
                </BarChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </Grid>

        {/* System Health Radar */}
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                System Health Overview
              </Typography>
              <ResponsiveContainer width="100%" height={400}>
                <RadarChart data={[
                  {
                    metric: 'Vehicle Count',
                    value: (analytics.activeVehicles / analytics.totalVehicles) * 100,
                    fullMark: 100,
                  },
                  {
                    metric: 'Speed',
                    value: (analytics.averageSpeed / 120) * 100,
                    fullMark: 100,
                  },
                  {
                    metric: 'Security',
                    value: analytics.systemHealth,
                    fullMark: 100,
                  },
                  {
                    metric: 'Network',
                    value: analytics.networkPerformance,
                    fullMark: 100,
                  },
                  {
                    metric: 'Fuel Efficiency',
                    value: analytics.fuelEfficiency,
                    fullMark: 100,
                  },
                ]}>
                  <PolarGrid />
                  <PolarAngleAxis dataKey="metric" />
                  <PolarRadiusAxis angle={30} domain={[0, 100]} />
                  <Radar name="System Health" dataKey="value" stroke={theme.palette.primary.main} fill={theme.palette.primary.main} fillOpacity={0.3} />
                  <RechartsTooltip />
                </RadarChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Analytics;
